'use client';

import React, { useState } from 'react';
import { ArrowRight } from 'lucide-react';

export default function ContratarPage() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [cpf, setCpf] = useState('');
  const [phone, setPhone] = useState('');
  const [agree, setAgree] = useState(false);

  const canSubmit = name && email && cpf && phone && agree;

  const redirectToCheckout = () => {
    const checkout = process.env.NEXT_PUBLIC_INFINITYPAY_CHECKOUT_URL;
    if (!checkout) {
      alert('Configure a vari√°vel NEXT_PUBLIC_INFINITYPAY_CHECKOUT_URL para habilitar o checkout do Infinity Pay.');
      return;
    }
    const params = new URLSearchParams({
      name,
      email,
      cpf,
      phone,
      amount: '14999',
      description: 'Plano de Treino Ricardo Team',
    });
    window.location.href = `${checkout}?${params.toString()}`;
  };

  return (
    <main className="min-h-screen bg-gray-50 flex items-center">
      <section className="w-full py-12">
        <div className="max-w-3xl mx-auto px-4">
          <div className="card-modern p-0 overflow-hidden">
            <div className="gradient-bg-primary text-white px-8 py-6 flex items-center justify-between">
              <div className="font-orbitron text-2xl font-bold">Cadastro e Pagamento</div>
              <div className="text-sm">Valor: <span className="font-semibold text-yellow-300">R$ 149,99</span></div>
            </div>

            <div className="p-8">
              <form onSubmit={(e) => { e.preventDefault(); redirectToCheckout(); }} className="space-y-6">
                <div className="grid md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-semibold text-gray-700 mb-3">Nome Completo</label>
                    <input
                      name="name"
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                      className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500"
                      placeholder="Seu nome completo"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-semibold text-gray-700 mb-3">E-mail</label>
                    <input
                      name="email"
                      type="email"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500"
                      placeholder="seu@email.com"
                    />
                  </div>
                </div>

                <div className="grid md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-semibold text-gray-700 mb-3">CPF</label>
                    <input
                      name="cpf"
                      value={cpf}
                      onChange={(e) => setCpf(e.target.value)}
                      className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500"
                      placeholder="000.000.000-00"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-semibold text-gray-700 mb-3">Telefone/WhatsApp</label>
                    <input
                      name="phone"
                      value={phone}
                      onChange={(e) => setPhone(e.target.value)}
                      className="w-full px-4 py-4 border-2 border-gray-200 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500"
                      placeholder="(11) 99999-9999"
                    />
                  </div>
                </div>

                <label className="flex items-center gap-3 text-gray-700 bg-gray-50 p-4 rounded-xl">
                  <input type="checkbox" checked={agree} onChange={(e) => setAgree(e.target.checked)} className="w-5 h-5" />
                  <span className="text-sm">Concordo em enviar o comprovante via WhatsApp ap√≥s concluir.</span>
                </label>

                <button
                  type="submit"
                  disabled={!canSubmit}
                  className="w-full btn-primary text-xl py-5 flex items-center justify-center gap-2"
                >
                  üí≥ Pagar R$ 149,99 - Infinity Pay
                  <ArrowRight className="w-5 h-5" />
                </button>
              </form>
            </div>
          </div>

          <div className="mt-6 flex justify-center">
            <a href="/" className="btn btn-outline">Voltar ao in√≠cio</a>
          </div>
        </div>
      </section>
    </main>
  );
}